@import '../common/sass/mixins';

.mainBlock {
    min-height: 150vh;
    background-color: #1c1f2c;

    .mainContainer {
        @media screen and (max-width: 835px) {
            justify-content: center;
        }

        .greeting {
            width: 300px;
            font-size: 40px;
            font-weight: 300;
            color: #ffffff;
            margin-bottom: 15px;
            margin-left: 15px;
        }

        .photo {
            width: 350px;
            @include image(500px);

        }
    }
}












